* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif;
  background-color: #f5f5f5;
}
nav {
  background-color: green;
  text-align: center;
  padding: 1rem;
  margin-bottom: 1rem;
}
button {
  width: 150px;
}
.commandBtns button {
  padding: 0.7rem 1rem;
}
.top-commandBtns button {
  background-color: hotpink;
}
.side-commandBtns {
  max-width: 200px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 1rem;
}
.side-commandBtns button {
  background-color: teal;
}
.side-commandBtns .run-btn {
  padding: 2rem;
  background-color: greenyellow;
  font-weight: 900;
  font-size: 2rem;
}
.charts-container {
  display: flex;
}

.charts-container .charts {
  max-width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.charts-container .charts div {
  margin: 1rem;
  padding: 1rem;
  background-color: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
#cost-flow,
#time-table {
  /* width: 50%; */
}
